/**
 * Check if the connection is working.
 * @example ```ts
 * aio.amIonline();
 * //=> false
 *
 * aio.amIonline();
 * //=> true
 *
 * aio.whenOffline( () => {
 *   alert('lost connection')
 * } )
 * ```
 * @param void
 */
export class aio {
  static amIonline(): boolean {
    return navigator.onLine;
  }

  static whenOffline(callback: () => void): void {
    setInterval(() => {
      if (!this.amIonline()) {
        callback();
      }
    }, 400);
  }
}

